.nav {
  grid-area: n;
  background-color: rgba(107, 73, 229, 0.51);
  padding: 15px;
  margin: 5px 5px 5px 10px;
  font-family: Georgia;
  border: 1px solid black;
  border-radius: 15px;
  text-align: center;
  font-size: 25px;
}
.item a {
  font-family: Georgia;
  text-decoration-line: unset;
  letter-spacing: 1px;
  color: white;
  margin-left: 5px;
}
.item a.active {
  color: rgb(54, 10, 36);
  text-shadow: none;
}
.item:hover {
  text-shadow: 2px 2px 3px black;
}
.sideBarTitle {
  padding: 30px 0 20px 0;
}
.sideBarItems {

}
.sideBarItem {
  width: 50px;
  padding: 2px;
}